/**
 * Simple test for isolated tokenUtils
 */

const tokenUtils = require('./simple-tokenUtils.ts');

describe('Simple TokenUtils', () => {
  test('formatAuthHeader should work', () => {
    const result = tokenUtils.formatAuthHeader('test');
    console.log('Simple formatAuthHeader result:', result);
    expect(result).toBe('Bearer test');
  });
  
  test('decodeJWTPayload should work', () => {
    const header = JSON.stringify({ alg: 'HS256', typ: 'JWT' });
    const payload = JSON.stringify({ sub: '123', exp: Math.floor(Date.now() / 1000) + 3600 });
    
    const encodedHeader = Buffer.from(header).toString('base64');
    const encodedPayload = Buffer.from(payload).toString('base64');
    const token = `${encodedHeader}.${encodedPayload}.signature`;
    
    const decoded = tokenUtils.decodeJWTPayload(token);
    console.log('Simple decoded result:', decoded);
    
    expect(decoded).not.toBeUndefined();
    expect(decoded).not.toBeNull();
    expect(decoded.sub).toBe('123');
  });
});